@font-face {
	font-family: Arita-buriM;
	src: url(../../../fonts/Arita-buriM.otf);
}

$font-list: Arita-buriM, sans-serif;

html {
	background-color: #362113;
	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
}

@mixin DefaultStyle($special){
	background-color: #362113;
	border: 3px solid #83502e;
	border-radius: 5px;

	color: #CE8B54;
	font-family: inherit;
	font-weight: 500;
	font-size: 16px;
	letter-spacing: 2px;

	height: 40px;

	transition: border 0.2s ease-in-out;

	&:focus { border: 3px solid #ce8b54; outline: none; }

	@if $special == "text" {
		padding: 0 10px;
		width: 90%;
	}

	@if $special == "select" {
		display: inline;
		float: left;
		width: 50%;
		margin-left: calc(5% - 14px);
		&option { background-color: #362113; color: #CE8B54; }
	}

	@if $special == "button" {
		display: inline;
		background-color: #362113;
		padding: 0;

		float: right;
		margin-right: calc(5% - 14px);

		width: 30%;
		
		transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out;

		&:hover { background-color: #714b2c; }
	}
}

#Main {
	text-align: center;
	font-family: $font-list;

	h1 { font-size: 60px; color: yellow; filter: blur(0.4px); letter-spacing: 6px; text-shadow: 1px 1px 8px #ffff9c; }

	input[type=text] { @include DefaultStyle("text"); }

	select { @include DefaultStyle("select"); }

	#advancedSetting { @include DefaultStyle("button"); }

	#pageList {
		margin: 2%;
		text-align: left;
		#List { 
			list-style-type: none; 
			padding-left: 0; 

			li {
				width: 33%;
				max-width: 33%;
				float: left;
			}
		}
	}
}

#Page {
	margin: 2%;
	height: 350px;
	display: flex;

	background-image: linear-gradient(rgba(0,0,0, 0.5) 80%, transparent);
	background-color: rgba(0,0,0, 0.3);
	border-style: solid;
	border-radius: 0;
	border-width: 0;

	color: white;
	text-shadow: 0px 0px 2px #ffff9c;
	box-shadow: 2px 2px 4px rgba(0,0,0, 0.3);

	border-style: solid;
	border-width: 4px 0 0 4px;
	border-radius: 3px;

	#first {
		flex: 0 0 40%;

		background-image: linear-gradient(to left, transparent 5%, rgba(0,0,0, 0.3));

		text-align: center;

		#pageName {
			line-height: 20px;
			margin: 10px 0;
			font-size: 20px;
			font-weight: 900;
			letter-spacing: 2px;
		}

		#pageImage {
			z-index: 2;
			width: 100%;
			border-style: solid;
			border-width: 4px 0 4px 0;
			border-radius: 4px;
		}

		#pageRange {
			margin: 10px 0;
			font-size: 16px;
			font-weight: 900;
			letter-spacing: 2px;
		}

		#pageCost {
			margin: 10px 0;
			font-size: 20px;
			font-weight: 900;
			letter-spacing: 2px;
		}
	}

	#second {
		border-style: solid;
		border-width: 0 0 0 1px;
		border-radius: 5px;

		flex: 1;
		padding: 10px;

		#pageDesc {
			font-size: 14px
		}

		#pageDice {
			margin-top: 16px;
			margin-bottom: 16px;
			vertical-align: middle;

			#diceRoll {
				display: block; 
				float: left; 
				width: 45px;
				vertical-align: middle;
				padding-top: 2.5px;

				text-align: center;
				font-size: 17px;
			}

			#diceIcon {
				width: 24px;
				height:	24px;

				vertical-align: middle;
			}

			#diceDesc {
				font-size: 14px;
				line-height: 20px;
			}
		}
	}
}

